<template lang="html">

  <v-container>


      <v-row  justify="center" align="center" class='row1' >
        <v-col cols='12' md='4' class='d-none d-md-flex justify-center'>
          <v-img class='skill' :src="require('@/assets/skill.svg')"
              max-width=250
              ></v-img>
        </v-col>
        <v-col  cols='12' sm='10' md='8' class="text-center">
          <span class="display-1 blue-grey--text text--lighten-1 pd-4">
            Skill Set
          </span>
            <v-card v-scrollanimation v-for="skill in skills"  :key="skill.title" :style="'transition-delay:'+ skill.delay">
              <v-card-title class='ml-3 font-weight-bold blue-grey--text text--darken-3'>{{skill.title}}</v-card-title>
              <v-card-text class="inline body-1 ml-3 md-3 blue-grey--text text--darken-3">
              <span v-for="name in skill.names"  :key="name" >
              <v-icon  x-small class='mr-2 ml-2 mb-1'>mdi-circle </v-icon>{{name}}
            </span>
          </v-card-text>
            </v-card>




            <v-btn
              rounded
              outlined
              class="mt-4"
              color="blue-grey" route to="/projects">
               My Projects
              </v-btn>

        </v-col>
      </v-row>

<v-divider class="mt-10"></v-divider>
  </v-container>
</template>

<script>
export default {
  name:'Skills',
  data(){
    return { skills:[
      {title:'Front-End',names:['HTML','CSS','Javascript'],delay:'0s'},
      {title:'Back-End',names:['PHP','SQL','MangodB'],delay:'.2s'},
      {title:'Programming Languages',names:['C and C++','Python','Java'],delay:'0.4s'}
      ]
    }
  }
}
</script>

<style lang="scss" scoped >

.before-enter{

    opacity: 0;
    transform: translateX(100px);
    transition: all 1s cubic-bezier(.36,.1,.58,1.23);}

  .enter{


    opacity: 1;
        transform: translateX(0px);}


</style>
